import React from 'react'
import { Modal, Button } from 'react-bootstrap';
import { useState } from 'react'

import '../../assets/widget/MainPageStyle.css'

import MainHeader from './partials/MainHeader';
import MainBody from './partials/MainBody';
import MainFooter from './partials/MainFooter';

const MainPage = () => {
    const [address, setAddress] = useState('')
    const [fullName, setFullName] = useState('');
    const [telegram, setTelegram] = useState('-');
    const [twitter, setTwitter] = useState('-');
    const [amount, setAmount] = useState(0.5);
    const [history, setHistory] = useState([]);

    const [walletModal, setWalletModal] = useState(false)

    const connectWallet = () => {
        setWalletModal(true)
    }

    const submit = () => {
        if(address === "") {
            alert("The wallet is not connected.");
            return;
        }
        if(fullName !== "") {
            console.log(fullName, telegram, twitter, amount)
        }
    }

    const changeValue = (e) => {
        switch(e.target.id) {
            case 'fullName':
                setFullName(e.target.value)
                break;
            case 'telegram':
                setTelegram(e.target.value)
                break;
            case 'twitter':
                setTwitter(e.target.value)
                break;
            case 'amount':
                setAmount(e.target.value.split('_')[1])
                break;
        }
    }

    return (
        <>
           <MainHeader connectWallet = { connectWallet } />
           <MainBody 
                fullName = { fullName }
                telegram = { telegram !== '-' ? telegram : '' }
                twitter = { twitter !== '-' ? twitter : '' }
                amount={ "BNB_" + amount }
                submit = { submit } 
                changeValue = { changeValue } 
            /> 
           <MainFooter history = { history }/>

            <Modal className="connectWalletModal" show={walletModal} onHide={()=>{setWalletModal(false)}}>
                <Modal.Header closeButton>
                    <Modal.Title>Connect a wallet</Modal.Title>
                </Modal.Header>
                <Modal.Body>
                    <Button className='walletButton'>
                        Metamask
                    </Button>
                    <Button className='walletButton'>
                        Wallet connect
                    </Button>
                </Modal.Body>
            </Modal>
        </>
    );
}

export default MainPage;
