import React from 'react'
import { Table } from 'react-bootstrap'

const HistoryTable = (props) => {
    return (
        <div className='display-table'>
            <div className="table-board">
                <Table style={{ width: '100%' }}>
                    <thead>
                        <tr>
                            <th style={{ width: '3%' }}>#</th>
                            <th style={{ width: '12%' }}>Name</th>
                            <th style={{ width: '32%' }}>Wallet Address</th>
                            <th style={{ width: '12%' }}>Telegram</th>
                            <th style={{ width: '11%' }}>Twitter</th>
                            <th style={{ width: '9%' }}>Amount</th>
                            <th style={{ width: '20%' }}>Txn</th>
                        </tr>
                    </thead>
                    <tbody>
                        {props.history.length > 0 ? 
                            props.history.map((transaction, index) => (
                                <tr>
                                    <td>index + 1</td>
                                    <td>transaction.name</td>
                                    <td>transaction.wallet_address</td>
                                    <td>transaction.telegram</td>
                                    <td>transaction.twitter</td>
                                    <td>transaction.amount</td>
                                    <td>transaction.txn</td>
                                </tr>
                            ))
                            : null
                        }
                    </tbody>
                </Table>
            </div>
        </div>
    )   
}

export default HistoryTable;