import React from 'react'
import Select from '../../../../components/Select'

const SubmitBoard = (props) => {
    return (
        <div className="input-board">
            <input 
                type = "text" 
                placeholder = "Full Name (Required)" 
                id ='fullName' 
                value = {props.fullName} 
                onChange = {(e) => props.changeValue(e)}>
            </input>

            <input 
                type = "text" 
                placeholder = "Telegram Username (Optional)" 
                id = 'telegram'
                value = {props.telegram}
                onChange = {(e) => props.changeValue(e)}>
            </input>

            <input 
                type = "text" 
                placeholder = "Twitter Username (Optional)" 
                id = 'twitter'
                value = {props.twitter}
                onChange = {(e) => props.changeValue(e)}>
            </input>

            <Select 
                amount = {props.amount}
                changeValue = {(obj) => props.changeValue(obj)}/>

            <div className = "btn-init btn-submit" onClick = {() => {props.submit()}}>Submit</div>
        </div>
    );
}

export default SubmitBoard;